:root {
  /* Reuse header variables for consistency */
  --bg-primary: #ffffff;
  --bg-secondary: #f8fafc;
  --bg-tertiary: #f1f5f9;

  --border-primary: #e2e8f0;
  --border-secondary: #cbd5e1;

  --text-primary: #0f172a;
  --text-secondary: #16202f;
  --text-tertiary: #14181c;

  --brand-primary: #2563eb;
  --brand-hover: #1d4ed8;
  --brand-light: #dbeafe;

  /* Footer specific */
  --footer-bg: #a1c4b0;
  --footer-text: #ffffff;
  --footer-heading: #ffffff;
  --footer-border: #1e293b;

  /* Spacing */
  --space-xs: 0.25rem;
  --space-sm: 0.5rem;
  --space-md: 1rem;
  --space-lg: 1.5rem;
  --space-xl: 2rem;
  --space-2xl: 3rem;

  /* Typography */
  --font-sans:
    -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue',
    Arial, sans-serif;

  /* Border Radius */
  --radius-sm: 0.375rem;
  --radius-md: 0.5rem;
  --radius-lg: 0.75rem;

  /* Transitions */
  --transition-fast: 150ms ease;
  --transition-base: 200ms ease;
}

/* Footer Base */
.footer {
  background: var(--footer-bg);
  color: var(--footer-text);
  font-family: var(--font-sans);
  margin-top: auto;
}

/* Main Footer Content */
.footer-main {
  max-width: 80rem; /* 1280px */
  margin: 0 auto;
  padding: var(--space-2xl) var(--space-xl);
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--space-2xl);
}

/* Footer Sections */
.footer-section {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

/* Brand Section */
.footer-brand {
  max-width: 300px;
}

.footer-logo {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--footer-heading);
  margin-bottom: var(--space-sm);
}

.footer-tagline {
  font-size: 0.9375rem;
  line-height: 1.6;
  color: var(--text-tertiary);
  margin-bottom: var(--space-lg);
}

.footer-contact {
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
}

.contact-item {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  font-size: 0.875rem;
}

.contact-icon {
  width: 1rem;
  height: 1rem;
  color: var(--brand-primary);
  flex-shrink: 0;
}

/* Footer Headings */
.footer-heading {
  font-size: 1rem;
  font-weight: 600;
  color: var(--footer-heading);
  margin-bottom: var(--space-sm);
  letter-spacing: 0.025em;
}

/* Footer Links */
.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
}

.footer-link {
  color: var(--footer-text);
  text-decoration: none;
  font-size: 0.875rem;
  padding: var(--space-xs) 0;
  transition: color var(--transition-fast);
  display: inline-block;
}

.footer-link:hover {
  color: var(--footer-heading);
  transform: translateX(2px);
}

/* Newsletter Form */
.newsletter-form {
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
}

.input-group {
  display: flex;
  gap: var(--space-sm);
}

.newsletter-input {
  flex: 1;
  padding: 0.75rem var(--space-md);
  border: 1px solid var(--footer-border);
  border-radius: var(--radius-md);
  background: rgba(255, 255, 255, 0.05);
  color: var(--footer-text);
  font-size: 0.875rem;
  transition: all var(--transition-base);
}

.newsletter-input:focus {
  outline: none;
  border-color: var(--brand-primary);
  background: rgba(255, 255, 255, 0.1);
}

.newsletter-input::placeholder {
  color: var(--text-tertiary);
}

.newsletter-button {
  padding: 0.75rem var(--space-lg);
  background: var(--brand-primary);
  color: white;
  border: none;
  border-radius: var(--radius-md);
  font-weight: 500;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all var(--transition-base);
  white-space: nowrap;
}

.newsletter-button:hover:not(:disabled) {
  background: var(--brand-hover);
  transform: translateY(-1px);
}

.newsletter-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.form-note {
  font-size: 0.75rem;
  color: var(--text-tertiary);
  line-height: 1.4;
}

/* Subscription Success */
.subscription-success {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  padding: var(--space-md);
  background: rgba(16, 185, 129, 0.1);
  border: 1px solid rgba(16, 185, 129, 0.3);
  border-radius: var(--radius-md);
}

.success-icon {
  width: 1.5rem;
  height: 1.5rem;
  background: #10b981;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

/* Footer Divider */
.footer-divider {
  height: 1px;
  background: var(--footer-border);
  width: 90%;
  max-width: 80rem;
  margin: 0 auto;
}

/* Footer Bottom */
.footer-bottom {
  padding: var(--space-lg) var(--space-xl);
}

.footer-bottom-content {
  max-width: 80rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
  align-items: center;
  justify-content: space-between;
}

.copyright {
  font-size: 0.875rem;
  color: var(--text-tertiary);
  text-align: center;
}

/* Social Links */
.social-links {
  display: flex;
  gap: var(--space-md);
}

.social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--footer-border);
  border-radius: var(--radius-md);
  color: var(--footer-text);
  text-decoration: none;
  transition: all var(--transition-base);
}

.social-link:hover {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  transform: translateY(-2px);
}

.social-link svg {
  width: 1rem;
  height: 1rem;
}

/* Payment Methods */
.payment-methods {
  display: flex;
  align-items: center;
  gap: var(--space-md);
  flex-wrap: wrap;
  justify-content: center;
}

.payment-label {
  font-size: 0.875rem;
  color: var(--text-tertiary);
}

.payment-icons {
  display: flex;
  gap: var(--space-sm);
  flex-wrap: wrap;
  justify-content: center;
}

.payment-method {
  padding: var(--space-xs) var(--space-sm);
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--footer-border);
  border-radius: var(--radius-sm);
  font-size: 0.75rem;
  color: var(--footer-text);
}

/* Responsive Design */
@media (min-width: 768px) {
  .footer-bottom-content {
    flex-direction: row;
    gap: var(--space-xl);
  }

  .footer-main {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (max-width: 767px) {
  .footer-main {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--space-xl);
    padding: var(--space-xl) var(--space-lg);
  }

  .input-group {
    flex-direction: column;
  }

  .newsletter-button {
    width: 100%;
  }

  .social-links {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .footer-main {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .footer-contact {
    align-items: center;
  }

  .contact-item {
    justify-content: center;
  }

  .payment-methods {
    flex-direction: column;
    gap: var(--space-sm);
  }

  .payment-icons {
    justify-content: center;
  }
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  .footer-link,
  .social-link,
  .newsletter-button {
    transition: none;
  }
}

.footer-link:focus-visible,
.social-link:focus-visible,
.newsletter-button:focus-visible,
.newsletter-input:focus-visible {
  outline: 2px solid var(--brand-primary);
  outline-offset: 2px;
  border-radius: var(--radius-sm);
}

/* Print Styles */
@media print {
  .footer {
    background: white !important;
    color: black !important;
    border-top: 1px solid #ccc;
  }

  .social-links,
  .newsletter-form,
  .payment-methods {
    display: none !important;
  }
}
