.transaction-container {
  max-width: 700px;
  width: 90%;
  margin: auto;
  padding: 2rem;
  background: #ffffffdd;
  border-radius: 12px;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.05);
  margin-right: 20rem;
}

.transaction-title {
  font-size: 1.75rem;
  margin-bottom: 1.5rem;
  font-weight: bold;
  text-align: center;
  color: #6ba689;
}

.transaction-error {
  color: red;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 500;
}

.transaction-balance {
  font-size: 1.1rem;
  margin-bottom: 1rem;
  color: #58b758;
}

.loading-text {
  text-align: center;
  font-size: 1.1rem;
}

.table-wrapper {
  overflow-x: auto;
}

.transaction-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 1rem;
  font-size: 0.95rem;
}

.transaction-table th,
.transaction-table td {
  border: 1px solid #1a1313;
  padding: 0.75rem;
  text-align: left;
}

.transaction-table th {
  background-color: #f3f4f6;
  font-weight: 600;
  color: #333;
}

.transaction-table tr:nth-child(even) {
  background-color: #fafafa;
}

.no-data {
  text-align: center;
  padding: 2rem;
  color: #555;
}

.pagination {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  font-size: 0.95rem;
}

.pagination button {
  background-color: #519771;
  border: none;
  width: 100px;
  padding: 0.6rem 1rem;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  transition: background-color 0.3s ease;
}

.pagination button:hover:enabled {
  background-color: #d1d5db;
}

.pagination button:disabled {
  cursor: not-allowed;
  opacity: 0.6;
}
