/* Reset and base styles */
body,
html {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f4f4f4;
  height: 100%;
}

/* Center container */
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 20px;
  box-sizing: border-box;
}

/* Main panel styling */
.panel {
  display: flex;
  flex-direction: row;
  width: 90%;
  max-width: 1000px;
  background-color: linear-gradient(
    to bottom,
    #95d4c0 0%,
    #c3cfe2 50%,
    #a1c4b0 100%
  );
  border-radius: 12px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}

/* Each sub-panel (info and form) */
.panel-info,
.panel-content {
  flex: 1;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* Info panel styles */
.panel-info {
  background: linear-gradient(to bottom, #d0e3da 0%, #7ca694 50%, #a1c4b0 100%);
  color: #fff;
}

.panel-info h1 {
  font-size: 2rem;
  margin-bottom: 15px;
}

.panel-info p,
.panel-info ul {
  margin-bottom: 20px;
}

.panel-info ul {
  padding-left: 20px;
  list-style-type: disc;
}

.switch-btn {
  background-color: linear-gradient(
    to bottom,
    #77b8af 0%,
    #98bdb3 50%,
    #8caca8 100%
  );
  color: #4b6cb7;
  border: none;
  padding: 10px 20px;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
  align-self: start;
  transition: background-color 0.3s;
}

.switch-btn:hover {
  background-color: #e0e0e0;
}

/* Form panel styles */
.panel-content h1 {
  font-size: 2rem;
  color: #333;
  margin-bottom: 10px;
}

.panel-content h2 {
  color: #555;
  margin-bottom: 10px;
}

.panel-content p {
  color: #777;
  margin-bottom: 20px;
}

/* Responsive handling */

@media screen and (max-width: 768px) {
  .panel {
    flex-direction: column;
    height: auto;
    max-height: 100%;
    overflow-y: auto;
  }

  .panel-info,
  .panel-content {
    padding: 20px;
  }

  .panel-info h1,
  .panel-content h1 {
    font-size: 1.5rem;
  }

  .panel-content h2 {
    font-size: 1.2rem;
  }

  .switch-btn {
    align-self: stretch;
    text-align: center;
  }

  form button[type='submit'],
  form button[type='button'] {
    width: 100%;
  }

  input {
    font-size: 0.95rem;
  }
}
