/* Welcome section */
.welcome-section {
  margin-bottom: 2.5rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid #eaeaea;
}

.welcome-section h1 {
  font-size: 2rem;
  color: #2c3e50;
  margin-bottom: 0.5rem;
}

.welcome-section .subtitle {
  color: #7f8c8d;
  font-size: 1.1rem;
}

/* Dashboard grid */
.dashboard-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(10px, 1fr));
  gap: 1.5rem;
}

/* Card styling */
.dashboard-card {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.05);
  padding: 1rem;
  transition:
    transform 0.3s ease,
    box-shadow 0.3s ease;
}

.dashboard-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.1);
}

.dashboard-card h2 {
  font-size: 1.3rem;
  color: #34495e;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
}

.card-icon {
  margin-right: 10px;
  color: #3498db;
}

/* Info rows */
.card-content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.info-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.info-label {
  font-weight: 500;
  color: #7f8c8d;
  display: flex;
  align-items: center;
  gap: 8px;
}

.info-value {
  font-weight: 600;
  color: #2c3e50;
}

/* Loading and error states */
.user-overview.loading,
.user-overview.error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 300px;
}

.loading-spinner {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.error-message {
  color: #e74c3c;
  font-weight: 500;
  font-size: 1.1rem;
}
/* Responsive styles */
